import React, { useState } from 'react'
import './css/Sectors.css';
import alceriumIcon from '../img/alcerium_icon.png';
import castorixIcon from '../img/castorix_icon.png';
import valterreIcon from '../img/valterre_icon.png';
import lyrisiusIcon from '../img/lyrisius_icon.png';
import lysandeIcon from '../img/lysande_icon.png';

function Sectors() {
    const [sector, setActiveSector] = useState("Alcerium");

    function showAlcerium() {
        setActiveSector("Alcerium");
    }
    function showCastorix() {
        setActiveSector("Castorix");
    }
    function showValterre() {
        setActiveSector("Valterre");
    }
    function showLyrisius() {
        setActiveSector("Lyrisius");
    }
    function showLysande() {
        setActiveSector("Lysande");
    }

    if (sector == "Alcerium"){
        return (
            <div className="details">
                <div className="section">
                    <br></br>
                    <h className="title">Alcerium</h>
                </div>
                <div className="icon_section">
                    <img src={alceriumIcon} className="active_icon" onClick={showAlcerium}/>
                    <img src={castorixIcon} className="nonactive_icon" onClick={showCastorix}/>
                    <img src={valterreIcon} className="nonactive_icon" onClick={showValterre}/>
                    <img src={lyrisiusIcon} className="nonactive_icon" onClick={showLyrisius}/>
                    <img src={lysandeIcon} className="nonactive_icon" onClick={showLysande}/>
                </div>
                <p className="excerpt">
                    <b>K</b>nown for its special healing flora and distinctive foods 
                    and spices, Alcerium is Koranin’s second largest territory. Otherwise 
                    named the spring sector, Alcerium is made up of a large rainforest 
                    bordered by a vast ocean territory and occupied largely by underwater 
                    creatures, along with those gifted with elemental or healing abilities.
                </p>
            </div>
        )
    }
    else if (sector == "Castorix") {
        return (
            <div className="details">
                <div className="section">
                    <br></br>
                    <h className="title">Castorix</h>
                </div>
                <div className="icon_section">
                    <img src={alceriumIcon} className="nonactive_icon" onClick={showAlcerium}/>
                    <img src={castorixIcon} className="active_icon" onClick={showCastorix}/>
                    <img src={valterreIcon} className="nonactive_icon" onClick={showValterre}/>
                    <img src={lyrisiusIcon} className="nonactive_icon" onClick={showLyrisius}/>
                    <img src={lysandeIcon} className="nonactive_icon" onClick={showLysande}/>
                </div>
                <p className="excerpt">
                    <b>A</b>lso known as the winter sector, Castorix is a snowy mountain region where most 
                    of humanity is structured underground or deep within the mountains. This is where Koranin’s 
                    flashiest infrastructure and technology is located, occupied heavily by creatures whose abilities 
                    resonate with the dark or light. Deeper underground lies the sector’s mines, where Koranin’s most 
                    precious minerals are procured. Stationed aboveground in the mountain ranges are military camps, 
                    crawling with shapeshifters and those of enhanced natural abilities to aid in hunt.
                </p>
            </div>
        )
    }

    else if (sector == "Valterre") {
        return (
            <div className="details">
                <div className="section">
                    <br></br>
                    <h className="title">Valterre</h>
                </div>
                <div className="icon_section">
                    <img src={alceriumIcon} className="nonactive_icon" onClick={showAlcerium}/>
                    <img src={castorixIcon} className="nonactive_icon" onClick={showCastorix}/>
                    <img src={valterreIcon} className="active_icon" onClick={showValterre}/>
                    <img src={lyrisiusIcon} className="nonactive_icon" onClick={showLyrisius}/>
                    <img src={lysandeIcon} className="nonactive_icon" onClick={showLysande}/>
                </div>
                <p className="excerpt">
                    <b>T</b>he most diverse sector and the heart of the trade industry between 
                    territories, Valterre lies at the center of Koranin. A tall mountain range 
                    rings the sector, making it difficult to attack and therefore a safeguard for 
                    the stronghold of knowledge and academia. Aside from its lively, bustling cities, 
                    Valterre is where most officials and High Mages are stationed, and where countless 
                    others come in search of opportunity.
                </p>
            </div>
        )
    }

    else if (sector == "Lyrisius") {
        return (
            <div className="details">
                <div className="section">
                    <br></br>
                    <h className="title">Lyrisius</h>
                </div>
                <div className="icon_section">
                    <img src={alceriumIcon} className="nonactive_icon" onClick={showAlcerium}/>
                    <img src={castorixIcon} className="nonactive_icon" onClick={showCastorix}/>
                    <img src={valterreIcon} className="nonactive_icon" onClick={showValterre}/>
                    <img src={lyrisiusIcon} className="active_icon" onClick={showLyrisius}/>
                    <img src={lysandeIcon} className="nonactive_icon" onClick={showLysande}/>
                </div>
                <p className="excerpt">
                    <b>S</b>pecializing in battle and strategy, the autumn sector’s brawns and brains 
                    largely outclass those of the other sectors. Consisting of floating abodes connected 
                    by wind passages, Lyrisius is arguably the most detested yet revered sector, for it 
                    is occupied by many elites and royals whose condescending attitudes are evenly backed 
                    by their eminence. Those blessed with flight ability and divination mastery often grace
                    the palaces of Lyrisius.
                </p>
            </div>
        )
    }

    else if (sector == "Lysande") {
        return (
            <div className="details">
                <div className="section">
                    <br></br>
                    <h className="title">Lysande</h>
                </div>
                <div className="icon_section">
                    <img src={alceriumIcon} className="nonactive_icon" onClick={showAlcerium}/>
                    <img src={castorixIcon} className="nonactive_icon" onClick={showCastorix}/>
                    <img src={valterreIcon} className="nonactive_icon" onClick={showValterre}/>
                    <img src={lyrisiusIcon} className="nonactive_icon" onClick={showLyrisius}/>
                    <img src={lysandeIcon} className="active_icon" onClick={showLysande}/>
                </div>
                <p className="excerpt">
                    <b>K</b>oranin’s resident precinct of summer, Lysande is the largest sector and 
                    consists of a vast desert territory dotted with a subregion of tropical islands
                    along its coast. Lysande serves as the main provider of Koranin’s textiles and 
                    weaponry forging, and is occupied largely by those of weather-related prowess, 
                    humans and other creatures alike.
                </p>
            </div>
        )
    }
}

export default Sectors;